import React from 'react';
import Button from './index';

/**
 * 버튼 컴포넌트
 * 
 * 버튼은 사용자 작업을 시작하거나 이벤트를 트리거하는 데 사용됩니다.
 * 
 * ## 사용 지침
 * 
 * - **Primary**: 화면에서 가장 중요한 작업에 사용합니다.
 * - **Secondary**: 덜 중요한 작업이나 대안을 제공할 때 사용합니다.
 * - **CTA (Call to Action)**: 사용자의 주의를 집중시켜야 하는 중요한 행동 유도에 사용합니다.
 * 
 * ## 접근성
 * 
 * - 버튼 텍스트는 의미 있고 명확해야 합니다.
 * - 아이콘만 사용하는 경우 반드시 적절한 aria-label을 제공해야 합니다.
 * - 키보드 사용자를 위해 포커스 스타일이 명확해야 합니다.
 */
